<template>
  <div id="detail">
    <detail-navbar/>
    <scroll class="content" ref="scroll">
      <detail-swiper :top-images="topImages"/>
      <detail-base-info :goods="goods"/>
      <detail-shop-info :shop="shop"/>
      <detail-goods-info :detail-info="detailInfo" @imageLoad="imageLoad"/>
      <detail-param-info :param-info="paramInfo"/>
    </scroll>
  </div>

</template>

<script>
import DetailNavbar from "@/views/detail/childComs/DetailNavbar";
import DetailSwiper from "@/views/detail/childComs/DetailSwiper";
import DetailBaseInfo from "@/views/detail/childComs/DetailBaseInfo";
import DetailShopInfo from "@/views/detail/childComs/DetailShopInfo";
import DetailGoodsInfo from "@/views/detail/childComs/DetailGoodsInfo";
import DetailParamInfo from "@/views/detail/childComs/DetailParamInfo";

import Scroll from "@/components/common/scroll/Scroll";

import {getDetail, Goods, Shop, GoodsParam} from "@/network/detail";

export default {
  name: "Detail",
  components: {
    DetailNavbar,
    DetailSwiper,
    DetailBaseInfo,
    DetailShopInfo,
    DetailGoodsInfo,
    Scroll,
    DetailParamInfo,
  },
  data() {
    return {
      iid: null,
      topImages: [],
      goods: {},
      shop: {},
      detailInfo: {},
      paramInfo: {},
      commentInfo: {},
      goodsList: [],
      themeTops: [],
      currentIndex: 0
    }
  },
  created() {
    //1.保存传入的iid
    this.iid = this.$route.params.iid

  //  2.根据iid请求详情数据
    getDetail(this.iid).then(res => {
      const data = res.result
    //  3.获取顶部的图片轮播数据
      console.log(res);
      this.topImages = data.itemInfo.topImages
      //4.获取商品数据
      this.goods = new Goods(data.itemInfo, data.columns, data.shopInfo.services)
    //  5.获取商家数据
      this.shop = new Shop(data.shopInfo)
    //  6.保存商品的详情数据
      this.detailInfo = data.detailInfo
    //  7.获取参数信息
      this.paramInfo = new GoodsParam(data.itemParams.info, data.itemParams.rule)
    })
  },
  methods: {
    imageLoad() {
      this.$refs.scroll.refresh()
    }
  }
}
</script>

<style scoped>
  #detail {
    position: relative;
    z-index: 9;
    background-color: #fff;
    height: 100vh;
  }

  .content {
    height: calc(100% - 44px);
    overflow: hidden;
  }
</style>
